<template>
  <div id="files__">
    <!-- 导航 -->
    <Breadcrumbs :items="items"/>
    <v-layout row>
      <v-flex xs12 sm12 >
        <v-card>
          <v-img :src="data.certified_files" height="150px" @click="img_( data.certified_files )" >
            <v-layout column fill-height>
              <v-spacer></v-spacer>
              <v-card-title class="pl-5 pt-5">
                <div class="display-1 pl-5 pt-5">{{ data.name }}</div>
              </v-card-title>
            </v-layout>
          </v-img>
          <v-list two-line>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">phone_iphone</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>手机号码</v-list-tile-title>
                <v-list-tile-sub-title>
                  <v-chip class="grey white--text darken-2" > {{ data.phone | cover }} 、 {{ location }} </v-chip>
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">perm_identity</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>姓名</v-list-tile-title>
                <v-list-tile-sub-title>
                  <v-chip class="grey white--text darken-2" > {{ data.name }} </v-chip>
                  <!-- <v-img :src="data.id_card"></v-img> -->
                  </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">school</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>医生的职称
                </v-list-tile-title>
                <v-list-tile-sub-title>
                  <v-chip class="grey white--text darken-2" v-if="data.level == 0" > 主任医师 </v-chip>
                  <v-chip class="grey white--text darken-2" v-else-if="data.level == 1" > 副主任医师 </v-chip>
                  <v-chip class="grey white--text darken-2" v-else-if="data.level == 2" > 主治医师 </v-chip>
                  <v-chip class="grey white--text darken-2" v-else-if="data.level == 3" > 住院医师 </v-chip>
                  <v-chip class="grey white--text darken-2" v-else-if="data.level == 4" > 助理医师 </v-chip>
                  </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">school</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title> 医生职称证明 </v-list-tile-title>
                <v-list-tile-sub-title>
                  <v-img :src="data.authentication_files"  @click="img_( data.authentication_files )" ></v-img>
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">contact_mail</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title> 医生执业证
                </v-list-tile-title>
                <v-list-tile-sub-title>
                  <!-- 执照 -->
                  <v-img :src="data.certified_files" @click="img_( data.certified_files )" ></v-img>
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">list_alt</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>擅长的疾病</v-list-tile-title>
                <v-list-tile-sub-title>
                  <v-chip class="grey white--text darken-2" >  {{ data.speciality }} </v-chip>
                  <!-- <v-chip class="grey white--text darken-2" > 工作性质: {{ data.intro.work_experience }} </v-chip>
                  <v-chip class="grey white--text darken-2" > 学术经验: {{ data.intro.academic_experience }} </v-chip> -->
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">list_alt</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>医生简历</v-list-tile-title>
                <v-list-tile-sub-title>
                  <v-chip class="grey white--text darken-2" >  {{ data.intro.post_title }} </v-chip>
                  <!-- <v-chip class="grey white--text darken-2" > 工作性质: {{ data.intro.work_experience }} </v-chip>
                  <v-chip class="grey white--text darken-2" > 学术经验: {{ data.intro.academic_experience }} </v-chip> -->
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">room</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title> 工作地点 </v-list-tile-title>
                <v-list-tile-sub-title>
                  <v-chip class="grey white--text darken-2" v-for="(item,index) in data.workplaces" :key="index+888" > {{ item.area }} - {{ item.name }} ( {{ item.longitude }} - {{ item.latitude }} ) </v-chip>
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">category</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>
                 状态
                </v-list-tile-title>
                <v-list-tile-sub-title>
                   <v-chip
                    v-if="data.pass_validation == 1"
                    :color="`red lighten-4`"
                    class="ml-0"
                    label
                    small
                  >未申请认证</v-chip>
                  <v-chip
                    v-else-if="data.pass_validation == 3"
                    :color="`red lighten-4`"
                    class="ml-0"
                    label
                    small
                    style="height: 60px !important;"
                  >认证被拒绝 (理由：{{ data.rejection_reason }} )</v-chip>
                  <v-chip
                    v-else-if="data.pass_validation == 2"
                    :color="`teal lighten-4`"
                    class="ml-0"
                    label
                    small
                  >审核中</v-chip>
                  <v-chip
                    v-else-if="data.pass_validation == 4"
                    :color="`teal lighten-4`"
                    class="ml-0"
                    label
                    small
                  >认证已通过</v-chip>
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">timer</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title> 创建时间 </v-list-tile-title>
                <v-list-tile-sub-title><v-chip class="grey white--text darken-2" > {{ data.created_time | time }} </v-chip></v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset></v-divider>
            <v-list-tile >
              <v-list-tile-action>
                <v-icon color="indigo">timer</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title> 审核操作时间 </v-list-tile-title>
                <v-list-tile-sub-title><v-chip class="grey white--text darken-2" > {{ data.authentication_time | time }} </v-chip></v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset v-if="data.pass_validation == 2"></v-divider>
            <v-list-tile  v-if="data.pass_validation == 2">
              <v-list-tile-action>
                <v-icon color="indigo">create_new_folder</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title></v-list-tile-title>
                <template v-for="(item,index) in root_path">
                  <v-list-tile-sub-title :key="index" v-if="item.url == '/api/v1/doctor/collate'" >
                    <v-btn v-show="!refuse" @click="refuse = true">拒绝通过</v-btn>
                    <v-btn v-show="!refuse" color="info" @click="doctor_collate( data.doctor_id, 2 )">审核通过</v-btn>
                  </v-list-tile-sub-title>
                </template>
              </v-list-tile-content>
            </v-list-tile>
            <!-- 拒绝通过原因 -->
            <v-alert  transition="scale-transition" v-show="refuse" :value="true" color="warning"  outline>
              请填写拒绝该医生通过审核的原因（200个汉字以内）
              <br>
              以上信息将会在APP内通知到医生！
              <!-- <v-text-field v-model="content" label="原因说明" type="text" maxlength="200" required></v-text-field> -->
              <v-textarea
                    v-model="content" 
                    outline
                    name="input-7-4"
                    label="输入描述"
                    value=""
                  ></v-textarea>
              <v-btn @click="refuse = false">暂不审核</v-btn>
              <v-btn color="info" @click="doctor_collate( data.doctor_id, 1 )">确认拒绝</v-btn>
              </v-alert>
          </v-list>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import { setTimeout } from "timers";
export default {
  components: {
    Breadcrumbs
  },
  data() {
    return {
      // 拒绝原因
      content:"",
      refuse:false,
      data: {
        doctor_id: 0,
        phone: "",
        name: "",
        head_url: "",
        thumb_url: "",
        level: 0,
        speciality: "",
        id_number: "",
        id_card: "",
        pass_validation: 0,
        rejection_reason: "",
        authentication_files: "",
        certified_files:"",
        authentication_time: 0,
        created_time: 0,
        update_time: 0,
        intro: {
          post_title: "",
          work_experience: "",
          academic_experience: ""
        },
        workplaces: [
          {
            area: "",
            name: ""
          }
        ],
      },
      items: [
        {
          text: "医生列表",
          disabled: false,
          to: "/doctorList"
        },
        {
          text: "医生详情",
          disabled: false,
          to: "/doctorInfo"
        }
      ],
      root_path: this.$cookie.getLocalStorage("root_path").root_path,
      // 所属地
      location:'获取所属地...'
    };
  },
  mounted: function() {
    this.info();
  },
  methods: {
    /** 图片预览 */
    img_( img ){
      window.open(img,'newwindow', `height=600, width=1000, top=${ 150 }, left=${ document.body.clientWidth/2 - 500 }, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=no, status=no`);
    },
    info() {
      this.data = this.$route.query.data;
      this.$axios({
        method: "post",
        url: this.$apis.URL.user_location,
        data: {
          phone: this.data.phone
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            if(response.data.datas.response[this.data.phone] !== null){
              this.location = response.data.datas.response[this.data.phone].location;
            }else{
              this.location = '获取位置失败'
            }
          }else{
            this.$Dialog.getProductCheckResult_({text: response.data.message, timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    },
    // 1:拒绝 2:通过认证
    doctor_collate(doctor_id, status_id) {
      if( status_id == 1 ){
        if( this.content.length == 0 ){
          this.$Dialog.getProductCheckResult_({text: "请填写拒绝该医生通过审核的原因",timeout: 3000, snackbar: true });
          return false;
         }
      }

      this.$axios({
        method: "post",
        url: this.$apis.URL.doctor_collate,
        data: {
          status: status_id,
          doctor_id: doctor_id,
          content: this.content
        }
      })
        .then(response => {
          if (response.data.result === 0) {
              this.$Dialog.getProductCheckResult_({text: status_id == 1 ? "该医生已拒绝通过审核" : "该医生审核已通过",timeout: 3000, snackbar: true });
            setTimeout(() => {
              window.history.back(-1);
            }, 1500);
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less">
#files__ {
  .v-list--two-line .v-list__tile {
    min-height: 72px;
    height: auto !important;
  }
  .v-list__tile__title {
    height: auto !important;
  }
  .v-list__tile__sub-title{
    white-space: normal;
  }
  .v-alert.v-alert--outline{
    border-radius: 3px;
    border: none !important;
  }
}
</style>
